
@font-face{font-family:'ratio';src:url("../assets/fonts/ratio.woff2") format("woff2"),url("../assets/fonts/ratio.woff") format("woff"),url("../assets/fonts/ratio.ttf") format("truetype");font-weight:normal;font-style:normal
}
.yellow{color:#facb16
}
a .yellow:hover{color:#FFE489
}
